import axios from 'axios'
import { Indicator, Toast } from 'mint-ui';
import store from '@/store'

// create an axios instance
const service = axios.create({
  baseURL: '/admin',
  // baseURL: process.env.BASE_API, // api  base_url
  // baseURL: location.hostname == 'localhost' ? '/admin' : 'http://47.93.228.120:8085', // api  base_url
  // baseURL: location.hostname == 'localhost' ? '/rest' : 'http://47.93.228.120:8085', // api  base_url
  timeout: 5000 // request timeout
})

// request interceptor
service.interceptors.request.use(
  config => {
    // Do something before request is sent
    // if (store.getters.token) {
    //   // token-- ['X-Token']key 
    //   // config.headers['X-Token'] = getToken()
    // }
    if(config.params && !config.params.hideLoadig) {
      Indicator.open({spinnerType: 'fading-circle'});
      // Indicator.open({text: '...', spinnerType: 'fading-circle'});
    }
    return config
  },
  error => {
    // Do something with request error
    console.log(error) // for debug
    Toast({
      message: '',
      position: 'bottom'
    });
    Promise.reject(error)
  }
)

// response interceptor
service.interceptors.response.use(
  response => {
    if(response.config.params && !response.config.params.hideLoadig) {
      Indicator.close();
    }
    return response;
  },
  /**
   * responsecode
   * code
   *  xmlhttprequest  error
   * 
   */
  // response => {
  //   const res = response.data
  //   if (res.code !== 20000) {
  //     Message({
  //       message: res.message,
  //       type: 'error',
  //       duration: 5 * 1000
  //     })
  //     // 50008:token; 50012:;  50014:Token ;
  //     if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
  //       //  MessageBox
  //       // import { Message, MessageBox } from 'element-ui'
  //       MessageBox.confirm('', '', {
  //         confirmButtonText: '',
  //         cancelButtonText: '',
  //         type: 'warning'
  //       }).then(() => {
  //         store.dispatch('FedLogOut').then(() => {
  //           location.reload() // vue-router bug
  //         })
  //       })
  //     }
  //     return Promise.reject('error')
  //   } else {
  //     return response.data
  //   }
  // },
  error => {
    Indicator.close();
    console.log('err' + error) // for debug
    Toast({
      message: error.message,
      position: 'bottom'
    });
    return Promise.reject(error)
  }
)

const request = {
  base: (config) => {
    return service.request(config)
  },
  get: (url, params) => {
    return service.get(url, {params: params});
  },
  post: (url, params) => {
    return service.post(url, params);
  }
}

export default request;



